
.sidebar {
    // flex: 0 0 18%;
    flex:1;
    border: 0.03125rem solid rgb(230, 227, 227); // 0.5px to rem
    min-height: 94.5vh;
    background-color: white;
    min-width: 15%;
    max-height: 100vh;
    overflow-y: auto;
    height: 100%;

    .top {
        height: 1.25rem; // 20px to rem
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin: 1.25rem 0 0.3125rem 1.25rem; // 20px 0 5px 20px to rem
        gap: 0.625rem; // 10px to rem

        .person {
            display: flex;
        }

        .person_name {
            font-size: 1rem; // 16px to rem
            font-weight: 500;
            line-height: 1.25rem; // 20px to rem
        }
    }

    .favourites_recent {
        .title {
            display: flex;
            justify-content: flex-start;
            margin: 0.9375rem 0 0 1.25rem; // 15px 0 0 20px to rem
            align-items: center;
            gap: 1.25rem; // 20px to rem
            font-weight: 500;

            .title_button {
                color: lightgray;
                font-size: 1rem; // 16px to rem
                border: none;
                text-decoration: none;
                background-color: white;
                cursor: pointer;
            }
        }

        .items_list {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;

            .item {
                margin-bottom: 0.3125rem; // 5px to rem
                color: lightgray;

                span {
                    color: black;
                    font-size: 0.9375rem; // 15px to rem
                    font-weight: 500;
                    line-height: 1.25rem; // 20px to rem
                }
            }
        }
    }

    .dashboard_Section {
        .dashboard_title {
            color: lightgray;
            font-size: 1rem; // 16px to rem
            font-weight: 500;
            margin: 0 0 0.3125rem 1.5625rem; // 0 0 5px 25px to rem
        }

        .dashboard_items_list {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            margin: 0.625rem 0 0.625rem 2.8125rem; // 10px 0 10px 45px to rem
        }

        .dashboard_item {
            display: flex;
            gap: 0.3125rem; // 5px to rem
            justify-content: flex-start;
            align-items: flex-start;
            margin-bottom: 0.4375rem; // 7px to rem
            width: 95%;
            cursor: pointer;
            text-decoration: none !important;
            color: inherit;

            .inner_item {
                display: flex;
                gap: 0.3125rem; // 5px to rem
               

                span {
                    color: black;
                    font-size: 0.9375rem; // 15px to rem
                    font-weight: 400;
                    line-height: 1.25rem; // 20px to rem
                }

               
        }
    }
        .selected {
                background-color: #f4f4f4; 
            color: white; 
            border-radius: 5px;
            padding: 4px;
            
        }
    
    }

    .pages_section {
        .page_title {
            color: lightgray;
            font-size: 1.0625rem; // 17px to rem
            font-weight: 500;
            margin: 0 0 0.3125rem 1.5625rem; // 0 0 5px 25px to rem
        }

        .pages_itemlist {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            margin: 0.625rem 0 0.625rem 2.8125rem; // 10px 0 10px 45px to rem
        }

        .dropdown_list {
            display: flex;
            flex-direction: column;
            margin: 0.125rem 0 0.5rem 2.5rem; // 2px 0 8px 40px to rem

            .dropdown_item {
                font-size: 0.9375rem; // 15px to rem
                line-height: 1.5625rem; // 25px to rem
            }
        }

        .page_item {
            display: flex;
            gap: 0.3125rem; // 5px to rem
            justify-content: flex-start;
            align-items: center;
            margin-bottom: 0.4375rem; // 7px to rem
            cursor: pointer;

            .inner_item {
                display: flex;
                gap: 0.3125rem; // 5px to rem

                span {
                    color: black;
                    font-size: 0.9375rem; // 15px to rem
                    font-weight: 400;
                    line-height: 1.25rem; // 20px to rem
                }
            }
        }
    }

    .arrow_icon {
        color: lightgray;
        font-size: 1.125rem; // 18px to rem
    }

    .icon {
        font-size: 1.125rem; // 18px to rem
        font-weight: 400;
        opacity: 0.7;
        color: #666;
    }

    .core_icon {
        width: 1.25rem; // 20px to rem
        height: 1.25rem; // 20px to rem
    }

    hr {
        height: 0;
        border: 0.03125rem solid rgb(230, 227, 227); // 0.5px to rem
    }
    @media (max-width: 768px) {
        .title{
            display: flex;
            flex-direction: column;
        }
        .dashboard_Section{
            .dashboard_items_list{
            margin: 5px;
            }
        }
        .pages_section{
            .pages_itemlist{
                margin: 5px;
            }
        }
       
     }
    
    }
